  .hide-scrollbar {
    overflow-y: scroll; /* Y축 스크롤은 허용 */
    scrollbar-width: none; /* Firefox용 - 스크롤바 숨기기 */
    background: transparent; /* 스크롤바 배경 투명화 */
  }
  
  .hide-scrollbar::-webkit-scrollbar {
    display: none; /* Chrome, Safari, Opera용 - 스크롤바 숨기기 */
    width:0px;
    scrollbar-width: none; /* Firefox용 - 스크롤바 숨기기 */
    background: transparent; /* 스크롤바 배경 투명화 */
  }

